<template>
  <div>
    How to Use?

    <div style="text-align: left">
      <div>
        1.  eg: lilyzhaoyilu
      </div>
      <idv>2. </idv>
      <div>3. Go</div>
      <div>~</div>
    </div>
    <a-input
      style="width: 300px;margin:0 20px;"
      placeholder=" Github "
      v-model="username"
    />
    <a-select
      style="width: 200px"
      v-model="currentDay"
      placeholder=""
    >
      <a-select-option
        v-for="solution in solutions"
        :value="solution.day"
        :disabled="!solution.body"
        :key="solution.day"
      >
        {{ `Day ${solution.day}` + solution.title }}
      </a-select-option>
    </a-select>

    <a-button class="btn" @click="load" type="primary"> Go </a-button>

    <a-button class="btn" @click="exportSolutions">  </a-button>

    <div v-if="dailyProblem">
      <Q :dailyProblem="dailyProblem" />
      <a-button type="link" @click="goToRenderRaw" target="_blank"
        ></a-button
      >
    </div>
  </div>
</template>

<script>
import writer from 'file-writer'

import Q from './QuestionDecription.vue'
import { setStorage } from '../../utils/storage'
import my from './db/my.json'
import dailyProblems from './db/solution.json'

export default {
  components: {
    Q
  },
  data() {
    return {
      username: undefined,
      currentDay: undefined,
      dailyProblem: undefined
    }
  },
  methods: {
    async goToRenderRaw() {
      await setStorage(
        'github-reader',
        my[this.username][this.currentDay - 1].body
      )
      this.$router.push('/solutionDetail?type=5')
    },
    load() {
      if (!this.currentDay) return
      this.dailyProblem = this.solutions[this.currentDay - 1]
    },
    exportSolutions() {
      writer(
        'solution.json',
        JSON.stringify(this.solutions.map(q => ({ ...q, content: undefined }))),
        {}
      )
    }
  },
  computed: {
    solutions() {
      return (my[this.username] || []).map((s, i) =>
        s ? { ...s, ...dailyProblems[i + 1] } : dailyProblems[i + 1]
      )
    }
  }
}
</script>

<style lang="less" scoped>
.btn {
  margin: 0 10px;
}
</style>
